import {
    LOGO, SEARCH_FAT, SHARE, STAR, STAR_12, STAR_16, USER_NAV_BARS, RED_TAG, SUPERHOST, BLACK_SUPERHOST_16, EYE,
    USER_NAV_PROFILE, LOCATION, KEY, CHECKIN, SEARCH, TICK_DECLINE, VERIFIED,
    ARROW_LEFT_FAT, ARROW_LEFT, ARROW_RIGHT, ARROW_RIGHT_FAT, ARROW_UP, CONFIG, EXIT, PLUS, MINUS, HEART_16_BLACK_STROKE, HEART_16_RED_FILL, HEART_24_WHITE_STROKE, HEART_24_WHITE_STROKE_RED_FILL
} from '../../services/svg.service.js'


// TODO: organize this atrocity


const SvgHandler = ({ svgName }) => {

    function svgIdentifier(name) {
        switch (name) {
            // ************************************** ARROWS **************************************
            case ARROW_LEFT_FAT:
                return <ArrowLeftFat />
            case ARROW_LEFT:
                return <ArrowLeft />
            case ARROW_RIGHT_FAT:
                return <ArrowRightFat />
            case ARROW_RIGHT:
                return <ArrowRight />
            case ARROW_UP:
                return <ArrowUp />

            // ************************************** SEARCH (Magnifying Glass) **************************************
            case SEARCH_FAT:
                return <SearchFat />
            case SEARCH:
                return <Search />

            // ************************************** CONFIG **************************************
            case CONFIG:
                return <Config />

            // ************************************** EXIT **************************************
            case EXIT:
                return <Exit />

            // ************************************** MATH OPERATIONS **************************************
            case PLUS:
                return <Plus />
            case MINUS:
                return <Minus />

            // ************************************** HEART **************************************
            case HEART_16_BLACK_STROKE:
                return <Heart16BlackStroke />
            case HEART_16_RED_FILL:
                return <Heart16RedFill />
            case HEART_24_WHITE_STROKE:
                return <Heart24WhiteStroke />
            case HEART_24_WHITE_STROKE_RED_FILL:
                return <Heart24WhiteStrokeRedFill />


            // ************************************** ... **************************************
            case USER_NAV_BARS:
                return <UserNavBar />
            case LOGO:
                return <Logo />
            case USER_NAV_PROFILE:
                return <UserNavProfile />
            case STAR:
                return <Star />
            case RED_TAG:
                return <RedTag />
            case STAR_12:
                return <Star12 />
            case STAR_16:
                return <Star16 />
            case SHARE:
                return <Share />
            case LOCATION:
                return <Location />
            case KEY:
                return <Key />
            case EYE:
                return <Eye />
            case CHECKIN:
                return <Checkin />
            case SUPERHOST:
                return <Superhost />
            case BLACK_SUPERHOST_16:
                return <BlackSuperhost16 />
            case 'TV':
                return <TV />
            case 'Wifi':
                return <Wifi />
            case 'Kitchen':
                return <Kitchen />
            case 'Pets allowed':
                return <Pets />
            case 'Dishes and silverware':
                return <Dishes />
            case 'Hot tub':
                return <HotTub />
            case 'Air conditioning':
                return <AirCond />
            case 'Gym':
                return <Gym />
            case 'Refrigerator':
                return <Refrigerator />
            case 'verified':
                return <Verified />
            case TICK_DECLINE:
                return <TickDecline />
            case 'exclamation':
                return <Exclamation />
            case 'Cable TV':
                return <TV />
            case 'Internet':
                return <Ethernet />
            case 'Wheelchair accessible':
                return <StainlessSteel />
            case 'Pool':
                return <Pool />
            case 'Free parking on premises':
                return <FreeParking />
            case 'Doorman':
                return <Doorman />
            case 'Elevator':
                return <MiniFridge />
            case 'Buzzer/wireless intercom':
                return <Wifi />
            case 'Heating':
                return <Heating />
            case 'Family/kid friendly':
                return <BeachEssentials />
            case 'Washer':
                return <Dishwasher />
            case 'Dryer':
                return <Dryer />
            case 'Smoke detector':
                return <SmokeDetector />
            case 'Carbon monoxide detector':
                return <CarbonMonoxide />
            case 'Essentials':
                return <Essentials />
            case 'Iron':
                return <Iron />
            case 'Free street door':
                return <FreeStreetParking />
            case 'Laptop friendly workspace':
                return <Wifi />
            case 'Private entrance':
                return <PrivateEntrance />
            case 'First aid kit':
                return <FirstAidKit />
            case 'Fire extinguisher':
                return <FireExtinguisher />
            case 'Shampoo':
                return <ShowerGel />
            case 'Lock on bedroom door':
                return <Safe />
            case 'Hangers':
                return <Hangers />
            // case 'Crib':
            //     return <Dishwasher />
            case 'Room-darkening shades':
                return <RoomDarkening />
            case 'Hot water':
                return <HotWaterKettle />
            case 'Bed linens':
                return <Bedlinens />
            case 'Extra pillows and blankets':
                return <ExtraPillows />
            case 'Microwave':
                return <Microwave />
            case 'Coffee maker':
                return <CoffeeMaker />
            case 'Dishwasher':
                return <Dishwasher />
            case 'Ethernet connection':
                return <Ethernet />
            case 'Window guards':
                return <WindowsGuards />
            case 'Babysitter recommendations':
                return <BabysitterRecommendations />
            case 'Beach essentials':
                return <BeachEssentials />
            case 'Children\'s books and toys':
                return <BooksReading />
            case 'Breakfast':
                return <Breakfast />
            // case 'Laptop friendly workspace':
            //     return <DedicatedWorkspace />
            case 'Garden or backyard':
                return <GardenView />
            case 'Keypad':
                return <Keypad />
            case 'Stove':
                return <InductionStove />
            case 'Long term stays allowed':
                return <LongTermStays />
            case 'Mountain view':
                return <MountainView />
            case 'Lockbox':
                return <Safe />
            case 'Self check-in':
                return <SelfCheckIn />

            default:
                return <HDTV />
        }
        // return null
    }

    return (svgIdentifier(svgName))
}

export default SvgHandler
